import React, { useState, useMemo } from 'react';
import { 
  Table, 
  TableHeader, 
  TableBody, 
  TableRow, 
  TableHead, 
  TableCell,
  Pagination,
  Dropdown,
  Button,
  Badge,
  Card,
  Input
} from '../ui';
import type { PaginationInfo, DropdownOption } from '../ui';

// ÊÑøÊúõÊï∞ÊçÆÁ±ªÂûã
export interface Wish {
  id: string;
  title: string;
  description: string;
  status: 'pending' | 'in_progress' | 'completed' | 'cancelled';
  priority: 'low' | 'medium' | 'high';
  createdAt: string;
  updatedAt: string;
  tags: string[];
}

// ÊÑøÊúõÂàóË°®ÁªÑ‰ª∂Â±ûÊÄß
interface WishListProps {
  wishes: Wish[];
  onWishUpdate?: (wish: Wish) => void;
  onWishDelete?: (wishId: string) => void;
  onWishStatusChange?: (wishId: string, status: Wish['status']) => void;
  className?: string;
}

// Áä∂ÊÄÅÈÄâÈ°π
const statusOptions: DropdownOption[] = [
  { value: 'all', label: 'ÂÖ®ÈÉ®Áä∂ÊÄÅ' },
  { value: 'pending', label: 'ÂæÖÂ§ÑÁêÜ' },
  { value: 'in_progress', label: 'ËøõË°å‰∏≠' },
  { value: 'completed', label: 'Â∑≤ÂÆåÊàê' },
  { value: 'cancelled', label: 'Â∑≤ÂèñÊ∂à' }
];

// ‰ºòÂÖàÁ∫ßÈÄâÈ°π
const priorityOptions: DropdownOption[] = [
  { value: 'all', label: 'ÂÖ®ÈÉ®‰ºòÂÖàÁ∫ß' },
  { value: 'high', label: 'È´ò‰ºòÂÖàÁ∫ß', icon: 'üî¥' },
  { value: 'medium', label: '‰∏≠‰ºòÂÖàÁ∫ß', icon: 'üü°' },
  { value: 'low', label: '‰Ωé‰ºòÂÖàÁ∫ß', icon: 'üü¢' }
];

// Áä∂ÊÄÅÊ†áÁ≠æÈ¢úËâ≤Êò†Â∞Ñ
const statusColorMap: Record<Wish['status'], string> = {
  pending: 'bg-yellow-100 text-yellow-800',
  in_progress: 'bg-blue-100 text-blue-800',
  completed: 'bg-green-100 text-green-800',
  cancelled: 'bg-gray-100 text-gray-800'
};

// Áä∂ÊÄÅÊ†áÁ≠æÊñáÊú¨Êò†Â∞Ñ
const statusTextMap: Record<Wish['status'], string> = {
  pending: 'ÂæÖÂ§ÑÁêÜ',
  in_progress: 'ËøõË°å‰∏≠',
  completed: 'Â∑≤ÂÆåÊàê',
  cancelled: 'Â∑≤ÂèñÊ∂à'
};

// ‰ºòÂÖàÁ∫ßÊ†áÁ≠æÈ¢úËâ≤Êò†Â∞Ñ
const priorityColorMap: Record<Wish['priority'], string> = {
  high: 'bg-red-100 text-red-800',
  medium: 'bg-yellow-100 text-yellow-800',
  low: 'bg-green-100 text-green-800'
};

// ‰ºòÂÖàÁ∫ßÊ†áÁ≠æÊñáÊú¨Êò†Â∞Ñ
const priorityTextMap: Record<Wish['priority'], string> = {
  high: 'È´ò',
  medium: '‰∏≠',
  low: '‰Ωé'
};

// ÊÑøÊúõÂàóË°®ÁªÑ‰ª∂
export const WishList: React.FC<WishListProps> = ({
  wishes,
  onWishUpdate,
  onWishDelete,
  onWishStatusChange,
  className
}) => {
  // Áä∂ÊÄÅÁÆ°ÁêÜ
  const [currentPage, setCurrentPage] = useState(1);
  const [itemsPerPage, setItemsPerPage] = useState(10);
  const [statusFilter, setStatusFilter] = useState<string>('all');
  const [priorityFilter, setPriorityFilter] = useState<string>('all');
  const [searchTerm, setSearchTerm] = useState('');
  const [sortField, setSortField] = useState<keyof Wish>('createdAt');
  const [sortDirection, setSortDirection] = useState<'asc' | 'desc'>('desc');

  // ËøáÊª§ÂíåÊéíÂ∫èÊÑøÊúõ
  const filteredAndSortedWishes = useMemo(() => {
    let filtered = wishes.filter(wish => {
      const matchesStatus = statusFilter === 'all' || wish.status === statusFilter;
      const matchesPriority = priorityFilter === 'all' || wish.priority === priorityFilter;
      const matchesSearch = searchTerm === '' || 
        wish.title.toLowerCase().includes(searchTerm.toLowerCase()) ||
        wish.description.toLowerCase().includes(searchTerm.toLowerCase()) ||
        wish.tags.some(tag => tag.toLowerCase().includes(searchTerm.toLowerCase()));
      
      return matchesStatus && matchesPriority && matchesSearch;
    });

    // ÊéíÂ∫è
    filtered.sort((a, b) => {
      const aValue = a[sortField];
      const bValue = b[sortField];
      
      if (typeof aValue === 'string' && typeof bValue === 'string') {
        return sortDirection === 'asc' 
          ? aValue.localeCompare(bValue)
          : bValue.localeCompare(aValue);
      }
      
      if (aValue instanceof Date && bValue instanceof Date) {
        return sortDirection === 'asc' 
          ? aValue.getTime() - bValue.getTime()
          : bValue.getTime() - aValue.getTime();
      }
      
      return 0;
    });

    return filtered;
  }, [wishes, statusFilter, priorityFilter, searchTerm, sortField, sortDirection]);

  // ÂàÜÈ°µËÆ°ÁÆó
  const totalItems = filteredAndSortedWishes.length;
  const totalPages = Math.ceil(totalItems / itemsPerPage);
  const startIndex = (currentPage - 1) * itemsPerPage;
  const endIndex = startIndex + itemsPerPage;
  const currentWishes = filteredAndSortedWishes.slice(startIndex, endIndex);

  // ÂàÜÈ°µ‰ø°ÊÅØ
  const paginationInfo: PaginationInfo = {
    currentPage,
    totalPages,
    totalItems,
    itemsPerPage,
    hasNextPage: currentPage < totalPages,
    hasPreviousPage: currentPage > 1
  };

  // Â§ÑÁêÜÊéíÂ∫è
  const handleSort = (field: keyof Wish) => {
    if (sortField === field) {
      setSortDirection(sortDirection === 'asc' ? 'desc' : 'asc');
    } else {
      setSortField(field);
      setSortDirection('asc');
    }
  };

  // Â§ÑÁêÜÁä∂ÊÄÅÂèòÊõ¥
  const handleStatusChange = (wishId: string, newStatus: Wish['status']) => {
    onWishStatusChange?.(wishId, newStatus);
  };

  return (
    <div className={className}>
      {/* ÊêúÁ¥¢ÂíåËøáÊª§Âå∫Âüü */}
      <Card className="mb-6">
        <div className="p-4">
          <div className="flex flex-col lg:flex-row gap-4">
            {/* ÊêúÁ¥¢Ê°Ü */}
            <div className="flex-1">
              <Input
                placeholder="ÊêúÁ¥¢ÊÑøÊúõÊ†áÈ¢ò„ÄÅÊèèËø∞ÊàñÊ†áÁ≠æ..."
                value={searchTerm}
                onChange={(e) => setSearchTerm(e.target.value)}
                className="w-full"
              />
            </div>
            
            {/* Áä∂ÊÄÅËøáÊª§ */}
            <Dropdown
              trigger={
                <Button variant="outline" className="min-w-[120px]">
                  {statusFilter === 'all' ? 'ÂÖ®ÈÉ®Áä∂ÊÄÅ' : statusTextMap[statusFilter as Wish['status']]}
                </Button>
              }
              options={statusOptions}
              onSelect={(option) => setStatusFilter(option.value as string)}
              width="sm"
            />
            
            {/* ‰ºòÂÖàÁ∫ßËøáÊª§ */}
            <Dropdown
              trigger={
                <Button variant="outline" className="min-w-[120px]">
                  {priorityFilter === 'all' ? 'ÂÖ®ÈÉ®‰ºòÂÖàÁ∫ß' : priorityTextMap[priorityFilter as Wish['priority']]}
                </Button>
              }
              options={priorityOptions}
              onSelect={(option) => setPriorityFilter(option.value as string)}
              width="sm"
            />
          </div>
        </div>
      </Card>

      {/* ÊÑøÊúõÂàóË°®Ë°®Ê†º */}
      <Card>
        <div className="overflow-x-auto">
          <Table>
            <TableHeader>
              <TableRow>
                <TableHead 
                  sortable 
                  sortDirection={sortField === 'title' ? sortDirection : null}
                  onSort={() => handleSort('title')}
                >
                  Ê†áÈ¢ò
                </TableHead>
                <TableHead>ÊèèËø∞</TableHead>
                <TableHead>Áä∂ÊÄÅ</TableHead>
                <TableHead>‰ºòÂÖàÁ∫ß</TableHead>
                <TableHead>Ê†áÁ≠æ</TableHead>
                <TableHead 
                  sortable 
                  sortDirection={sortField === 'createdAt' ? sortDirection : null}
                  onSort={() => handleSort('createdAt')}
                >
                  ÂàõÂª∫Êó∂Èó¥
                </TableHead>
                <TableHead>Êìç‰Ωú</TableHead>
              </TableRow>
            </TableHeader>
            <TableBody>
              {currentWishes.map((wish) => (
                <TableRow key={wish.id}>
                  <TableCell className="font-medium">{wish.title}</TableCell>
                  <TableCell className="max-w-xs truncate">{wish.description}</TableCell>
                  <TableCell>
                    <Badge className={statusColorMap[wish.status]}>
                      {statusTextMap[wish.status]}
                    </Badge>
                  </TableCell>
                  <TableCell>
                    <Badge className={priorityColorMap[wish.priority]}>
                      {priorityTextMap[wish.priority]}
                    </Badge>
                  </TableCell>
                  <TableCell>
                    <div className="flex flex-wrap gap-1">
                      {wish.tags.map((tag, index) => (
                        <Badge key={index} variant="outline" className="text-xs">
                          {tag}
                        </Badge>
                      ))}
                    </div>
                  </TableCell>
                  <TableCell>
                    {new Date(wish.createdAt).toLocaleDateString('zh-CN')}
                  </TableCell>
                  <TableCell>
                    <div className="flex gap-2">
                      <Dropdown
                        trigger={
                          <Button size="sm" variant="outline">
                            Áä∂ÊÄÅ
                          </Button>
                        }
                        options={statusOptions.filter(opt => opt.value !== 'all')}
                        onSelect={(option) => handleStatusChange(wish.id, option.value as Wish['status'])}
                        width="sm"
                      />
                      <Button 
                        size="sm" 
                        variant="outline"
                        onClick={() => onWishUpdate?.(wish)}
                      >
                        ÁºñËæë
                      </Button>
                      <Button 
                        size="sm" 
                        variant="destructive"
                        onClick={() => onWishDelete?.(wish.id)}
                      >
                        Âà†Èô§
                      </Button>
                    </div>
                  </TableCell>
                </TableRow>
              ))}
            </TableBody>
          </Table>
        </div>
        
        {/* ÂàÜÈ°µ */}
        {totalPages > 1 && (
          <div className="p-4 border-t">
            <Pagination
              info={paginationInfo}
              onPageChange={setCurrentPage}
              onItemsPerPageChange={setItemsPerPage}
              showItemsPerPage
              showTotal
              showPageInfo
            />
          </div>
        )}
      </Card>
    </div>
  );
};

// ÈªòËÆ§ÂØºÂá∫
export default WishList;
